#wrap {
	width: 100%;
}

#wrap h1 {
	width: 100%;
	line-height: 100px;
	font-size: 50px;
	color: rgb(219, 219, 171);
}

.center {
	width: 90%;
	min-width: 1500px;
	max-width: 1700px;
	display: flex;
	justify-content: flex-start;
	margin: 0 auto;
}

.center .project {
	display: block;
	color: rgb(176, 219, 252);
	font-weight: 600;
	width: 20%;
	height: 100px;
	line-height: 100px;
	background-color: rgba(30, 30, 30, 1);
	border-radius: 10px;
	margin: 10px;
	box-sizing: border-box;
	font-size: 35px;
}

.center .project::after {
	color: rgb(176, 219, 252);
}
