#wrap {
	width: 100%;
}

.contact-center {
	width: 90%;
	margin: 0 auto;
	display: flex;
	flex-flow: column wrap;
}
.contact-center h3 {
	text-align: center;
	width: 100%;
	font-size: 40px;
	margin-top: 40px;
}

.contact-center .img-box {
	background: url(/public/img/profile.jpeg) no-repeat center/cover;
	width: 300px;
	height: 300px;
	margin: 50px auto;
}

.contact-center .profile h4 {
	font-size: 25px;
}

.contact-center .profile p {
	padding: 10px 0;
	margin: 0;
	font-size: 20px;
}
