#wrap {
	width: 90%;
	text-align: center;
	color: #fff;
	margin: 0 auto;
}

p {
	/* display: inline-block; */
	font-size: 40px;
	color: white;
	padding: 200px 0 0 0;
}

span {
	font-size: 40px;
	color: rgb(173, 215, 247);
}

.digital-clock {
	font-size: 60px;
	display: block;
	height: 400px;
}
#wrap .typing {
	display: inline-block;
	font-size: 40px;
}

#wrap p {
	display: inline;
	color: rgb(173, 215, 247);
}
#wrap .intro {
	display: block;
	line-height: 500px;
}

.about {
	opacity: 1;
	width: 100%;
	height: 800px;
}

.about h4 {
	width: 100%;
	font-size: 40px;
	padding-bottom: 100px;
	box-sizing: border-box;
}
.about .about-box {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}

.about .about-box .left {
	width: 50%;
	display: flex;
	justify-content: right;
	padding-right: 10px;
	box-sizing: border-box;
}

.about .about-box .left .img-box {
	width: 400px;
	height: 400px;
	overflow: hidden;
}

.about .about-box .left .img-box img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.about .about-box .right {
	width: 50%;
	height: 400px;
	text-align: left;
	padding-left: 10px;
	box-sizing: border-box;
	position: relative;
}
.about .about-box .right h4 {
	font-size: 40px;
	line-height: 70px;
	width: 100%;
	padding: 0;
}

.about .about-box .right p {
	padding: 0;
	width: 100%;
	font-size: 25px;
	line-height: 30px;
	height: 30px;
}

#skill {
	width: 100%;
	height: 800px;
	box-sizing: border-box;
}
#skill h4 {
	width: 100%;
	font-size: 40px;
	box-sizing: border-box;
}

#skill .center {
	width: 60%;
	height: 100%;
	margin: 0 auto;
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	align-items: center;
}

#skill .center .img-box {
	width: 25%;
	padding: 50px;
	box-sizing: border-box;
}

#skill .center .img-box img {
	width: 100%;
}

#project {
	padding-top: 100px;
	width: 100%;
	height: 800px;
}

#project h4 {
	width: 100%;
	font-size: 40px;
	padding-top: 50px;
}

#project .project-box {
	width: 90%;
	margin: 0 auto;
	display: flex;
	justify-content: flex-start;
	padding-top: 40px;
}

#project .project-box a {
	width: 20%;
	color: rgb(173, 215, 247);
	line-height: 60px;
	background-color: #333;
	border-radius: 10px;
	margin: 10px;
	box-sizing: border-box;
	font-size: 30px;
	font-weight: 600;
}
