a {
	color: white;
	line-height: 50px;
}

header {
	width: 100%;
	height: 50px;
	background-color: #333;
	position: fixed;
}

header .center {
	width: 90%;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
}

header .center h1 {
	width: 200px;
	color: white;
	line-height: 45px;
}

header .center .menu {
	width: 400px;
	display: flex;
	justify-content: space-around;
}
